<template>
  <div class="tree">
    <li class="box">
      <div
        @click="toggle(model)"
        class="row"
        :style="{'text-indent': 2 * model.level + 'em'}"
        >
        <span v-if="isFolder"> {{model.name}}</span>
        <label v-else class="label_box">
          <input type="checkbox" :checked="computedCheck" name="user" disabled/>
          <img :src="model.avatar" alt="头像" v-if="model.avatar">
          <span>{{model.name}}</span>
          <span>{{model.jobnumber}}</span>
          <span>{{model.position}}</span>
        </label>
        <span v-if="isFolder" class="ricon">
          <img src="@/assets/imgs/xiasanjiao.png" alt="" v-if="open" class="xia">
          <img src="@/assets/imgs/qianjin.png" alt="" v-else class="zuo">
        </span>
      </div>
      <div class="hr"></div>
      <ul v-show="open" v-if="isFolder">
        <tree
          v-for="(model, index) in model.children"
          @selectUser="selectUser"
          :key="index"
          :model="model">
        </tree>
      </ul>
    </li>
  </div>
</template>

<script>
export default {
  name: 'Tree',
  props: {
    model: Object
  },
  inject: ['users'],
  data () {
    return {
      open: false,
      checked: null
    }
  },
  computed: {
    isFolder () {
      return this.model.children
    },
    computedCheck: {
      get () {
        let res = this.users.filter(item => {
          return item.jobnumber === this.model.jobnumber
        })
        return this.checked === null ? res.length > 0 : this.checked
      },
      set (v) {
        this.checked = v
      }
    }
  },
  methods: {
    toggle (model) {
      if (this.isFolder) {
        if (this.model.children.length === 0) {
          this.getUser(this.model.id)
          this.open = true
        } else {
          this.open = !this.open
        }
      } else {
        let user = this.users.filter(item => {
          return item.id === this.model.id
        })
        if (user.length === 0) {
          if (!model.jobnumber) {
            alert('请先给此人添加工号')
            return
          }
          this.users.push(this.model)
          this.computedCheck = true
        } else {
          let i = this.users.indexOf(user[0])
          i > -1 && this.users.splice(i, 1)
          this.computedCheck = false
        }
        this.$emit('selectUser', this.users)
      }
    },
    getUser (id) {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_department', 'get_users_by_department_id', [], {
        departmentid: id
      }).then(res => {
        let tempArr = []
        res.forEach(data => {
          tempArr.push({
            name: data.name,
            id: data.id,
            departmentId: data.departmentId,
            jobnumber: data.jobnumber,
            avatar: data.avatar,
            position: data.position,
            isworker: true,
            level: this.model.level + 1,
            select: false
          })
        })
        this.$set(this.model, 'children', tempArr)
        this.open = true
      }).catch(e => {
        console.log(e)
      })
    },
    selectUser () {
      this.$emit('selectUser', this.users)
    }
  },
  mounted () {
  }
}
</script>

