<template>
  <div class="kanban_add">
    <div class="box first arrow">
      <div class="row" @click="showSelcetSendee">
        <div class="left">
          任务接收人:
        </div>
        <div class="right" :class="{'placeholder': !sendee}">
          <span>{{sendee ? sendee: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          任务类型:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.category}">
          <span>{{this.formData.category ? this.formData.category.name: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="category" id="category" v-model="formData.category">
            <option value=""></option>
            <option :value="item" v-for="item in category" :key="item.id">{{item.name}}</option>
          </select>
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          优先级:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.level}">
          <span>{{this.formData.level ? this.formData.level: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="level" id="level" v-model="formData.level">
            <option value=""></option>
            <option value="高">高</option>
            <option value="中">中</option>
            <option value="低">低</option>
          </select>
        </div>
      </div>
    </div>
    <div class="box second arrow">
      <div class="row">
        <div class="left">
          开始时间:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.beginTime}">
          <span>{{this.formData.beginTime ? formatDateTime(this.formData.beginTime): '请选择开始时间'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <input type="datetime-local" v-model="formData.beginTime">
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          结束时间:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.deadLine}">
          <span>{{this.formData.deadLine ? formatDateTime(this.formData.deadLine): '请选择结束时间'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <input type="datetime-local" v-model="formData.deadLine">
        </div>
      </div>
    </div>
    <div class="box third">
      <div class="row">
        任务描述:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="" rows="10" v-model="formData.desc" placeholder="请输入问题"></textarea>
      </div>
    </div>
    <common-btn class="add_btn" @click="commitChange">确认提交</common-btn>
    <div class="blank"></div>
    <my-dialog v-if="isSelcetSendeeShow">
      <architecture @selectUser="selectUser"></architecture>
    </my-dialog>
  </div>
</template>

<script>
import Architecture from './components/Architecture'
const PRIORITY = 'priority'
const INTERMEDIATE = 'intermediate'
const ELEMENTARY = 'elementary'
export default {
  name: 'KanbanAdd',
  components: {
    Architecture
  },
  data () {
    return {
      isSelcetSendeeShow: false,
      category: [],
      formData: {
        sendee: [],
        category: '',
        level: '',
        beginTime: '',
        deadLine: '',
        desc: ''
      }
    }
  },
  computed: {
    sendee () {
      if (this.formData.sendee && this.formData.sendee.length > 0) {
        return this.formData.sendee.map((item) => {
          return item.name
        }).join(',')
      }
    }
  },
  methods: {
    showSelcetSendee () {
      this.isSelcetSendeeShow = true
    },
    getPriority (v) {
      switch (v) {
        case '高':
          return PRIORITY
        case '中' :
          return INTERMEDIATE
        case '低':
          return ELEMENTARY
        default:
          break
      }
    },
    // 格式化时间
    formatDateTime (v) {
      if (v) {
        return this.$moment(v).format('YY-MM-DD HH:mm:ss')
      }
    },
    commitChange () {
      let sendeeId = this.formData.sendee.map(item => {
        return item.id
      })
      this.$rpc.call('funenc_xa_station.work_kanban', 'app_save_work_kanban', [], {
        ids: sendeeId,
        task_type_id: this.formData.category.id,
        task_priority: this.getPriority(this.formData.level),
        task_start_time: this.formData.beginTime,
        task_end_time: this.formData.deadLine,
        task_describe: this.formData.desc
      }).then(res => {
        if (res) {
          this.$router.push({
            path: '/kanban'
          })
        }
      }).catch(e => {
        console.log(e)
      })
    },
    getCategory () {
      this.$rpc.call('funenc_xa_station.task_type', 'get_type_all', [], {}).then(res => {
        this.category = res
      }).catch(e => {
        console.log(e)
      })
    },
    selectUser (users) {
      this.formData.sendee = users
      this.isSelcetSendeeShow = false
    }
  },
  mounted () {
    this.getCategory()
  }
}
</script>

