<template>
  <div class="architecture">
    <div class="scroll_content_box">
      <div class="box" v-for="item in showDepartment" :key="item.id">
        <ul>
          <tree
            :model="item"
            @selectUser="selectUser"
            >
          </tree>
        </ul>
      </div>
      <common-btn class="add_btn" @click="commitChange">确认选择</common-btn>
    </div>
  </div>
</template>

<script>
import Tree from './Tree'
export default {
  name: 'Architecture',
  components: {
    Tree
  },
  provide: {
    users: []
  },
  data () {
    return {
      parentid: 1,
      department: [],
      users: []
    }
  },
  computed: {
    showDepartment: {
      get () {
        let department = JSON.parse(JSON.stringify(this.department))
        return this.getTree(department, this.parentid)
      }
    }
  },
  methods: {
    getTree (array, parentid = 1, tempArr = [], level = 0) {
      for (let i = 0; i < array.length; i++) {
        if (array[i]['parentid'] === parentid) {
          array[i]['children'] = []
          array[i]['level'] = level
          tempArr.push(array[i])
          this.getTree(array, array[i]['departmentId'], array[i]['children'], level + 1)
        }
      }
      return tempArr
    },
    getDeparment () {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_department', 'get_xa_departments', [], {}).then(res => {
        this.department = res.departments
        this.parentid = res.root_department
      }).catch(e => {
        console.log(e)
      })
    },
    selectUser (users) {
      this.users = users
    },
    commitChange () {
      this.$emit('selectUser', this.users)
    }
  },
  mounted () {
    this.getDeparment()
  }
}
</script>

