  .scroll_content_box{
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
    height: 100%;
    box-sizing: border-box;
    padding-top: 16 * @px1;
    .add_btn{
      margin-top: 10 * @px1;
    }
  }
}
</style>
